namespace $ {
	export class $ss_linkpad_link_type extends $ss_linkpad_link_default {
		
		/**
		 * ```tree
		 * icon \
		 * ```
		 */
		icon() {
			return ""
		}
		
		/**
		 * ```tree
		 * Label $mol_view sub /
		 * 	<= Name
		 * 	<= Link_id
		 * ```
		 */
		@ $mol_mem
		Label() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.Name(),
				this.Link_id()
			] as readonly any[]
			
			return obj
		}
	}
	
}

