namespace $ {
	export class $ss_linkpad_link_page extends $ss_linkpad_link_default {
		
		/**
		 * ```tree
		 * value_and_id /
		 * 	<= Value
		 * 	<= Link_id
		 * ```
		 */
		value_and_id() {
			return [
				this.Value(),
				this.Link_id()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * sub / <= Page
		 * ```
		 */
		sub() {
			return [
				this.Page()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Page $mol_page
		 * 	head /
		 * 		^ from_view
		 * 		^ self_view
		 * 	body / ^ links_view
		 * ```
		 */
		@ $mol_mem
		Page() {
			const obj = new this.$.$mol_page()
			
			obj.head = () => [
				...this.from_view(),
				...this.self_view()
			] as readonly any[]
			obj.body = () => [
				...this.links_view()
			] as readonly any[]
			
			return obj
		}
	}
	
}

